import React from 'react';
import './VerifyPage.scss';
import Spinner from '../../sharedComponents/functionalComponents/spinner/Spinner';
import utils from '../../services/utils.js';

import axios from 'axios';

interface State {
  loaded:boolean,
  hasToken:boolean,
  tokenVerified:boolean,
  emailVerifyError:boolean
}

class VerifyPage extends React.Component {
  public state:State;
  private lastQuery:string = "";
  private cancelTokenSource:any;
  
  constructor(props) {
    super(props);
    this.state = {
      loaded:false,
      hasToken:false,
      tokenVerified:false,
      emailVerifyError:false
    };
  }
  
  successFn() {
    this.setState({tokenVerified:true});
  }
  
  componentDidMount() {
    let redirectUrl = "/", parts:any;
    const authToken = localStorage.getItem("authToken");
    let token = "";
    window.location.search.substring(1).split("&").forEach((item) => {
      parts = item.split("=");
      if(parts[0] === "token") {
        token = parts[1];
        redirectUrl += "?token="+token;
        this.setState({hasToken:true});
        //TODO:call verify email service
      }
    });
    if(!authToken) {
      window.location.href = redirectUrl;
    }
    else {
      //user logged in so verify the token
      setTimeout(()=>{
        this.setState({loaded:true});
      },0);

      let parsed = utils.parseJwt(authToken);
      if(parsed.isEmailVerified) {
        this.successFn();
      }
      else {
        const url = "/api/verify?token="+token;
        if(this.cancelTokenSource)
          this.cancelTokenSource.cancel();
        this.cancelTokenSource = axios.CancelToken.source();
    
        axios.get(url, {
          cancelToken: this.cancelTokenSource.token
        })
        .then((d)=>{
          this.successFn();
        })
        .catch((error) => {
          console.error(error);
          this.setState({emailVerifyError:true});
        })
        .then(() => {
          
        });
      }
    }
  }
  
  render() {
    return (
      <section className="verifyPage">
       <div className="row">
        <div className="col-md-12">
          <div className={"content " + (this.state.loaded ? "" : " hidden")}>
            {!this.state.emailVerifyError && this.state.hasToken && !this.state.tokenVerified && <div className="alert alert-secondary" role="alert">
                <i>Your email has not been verified yet, please wait...</i>
            </div>}
            {!this.state.emailVerifyError && this.state.tokenVerified && <div className="alert alert-success" role="alert">
                <strong>Success,</strong> <i>Your email has been verified!</i>
            </div>}
            {!this.state.emailVerifyError && this.state.tokenVerified && <img className="successImg" src={process.env.PUBLIC_URL + "/phone_login.png"} />}
            {this.state.emailVerifyError && <div className="alert alert-danger" role="alert">
                There was an error verifying the token.
            </div>}
            {!this.state.hasToken && <div className="alert alert-danger" role="alert">
                Error, token not found.
            </div>}
          </div>
        </div>
       </div>
      </section>
    );
  }
}

export default VerifyPage;